/* HEADER COMPONENT CSS - STARTS */
.main-header {
  background-color: var(--color-dark);
  padding: 0 5rem;
  font-size: xx-large;
}

.header-container {
  margin: 0 7rem;
}

.header-container .title {
  display: flex;
  align-items: center;
}

.title-logo {
  margin-right: 1rem;
  color: #71c6dd;
}

.home-container {
  height: 100%;
  background-color: var(--color-bg);
}

.nav-wrapper {
  width: 25%;
}
/* HEADER COMPONENT CSS - ENDS */

/* NAVBAR COMPONENT CSS - STARTS */

.content-wrapper {
  display: flex;
  height: 100%;
  width: 100%;
  position: fixed;
  padding: 0 12rem;
  margin-top: 2rem;
}

.category-header {
  text-transform: uppercase;
  font-weight: 500;
  color: var(--color-dark-light);
  font-size: 1.5em;
  line-height: 2;
  text-align: left;
}

.compartments {
  margin: 1rem 0;
}

.compartment-header {
  color: #696d9791;
}

.compartment-item {
  color: var(--color-light-gray);
  font-size: 1.1rem;
  margin: 0.7rem 0;
  cursor: pointer;
}

.compartment-item.active {
  color: var(--color-main);
}

/* NAVBAR COMPONENT CSS - ENDS */

/* EVENT COMPONENT CSS - ENDS */

.event-content-wrapper {
  display: block;
  position: relative;
  width: 75%;
  padding: 0 2rem;
}

.screen-title {
  text-transform: uppercase;
  font-weight: 500;
  color: var(--color-light);
  letter-spacing: 1px;
}

.event-content-header {
  height: 3.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.create-new {
  background-color: var(--color-main);
  color: var(--color-dark);
  border: none;
  padding: 0 1.8rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  min-width: 10rem;
  top: 0;
  right: 8rem;
  font-size: 1.4rem;
  height: 3.5rem;
}

.create-new span {
  font-size: 2rem;
  margin-right: 5px;
}

.screen-content-counts {
  color: var(--color-dark-light);
}

.event-wrapper {
  margin: 3rem 0;
  display: flex;
  grid-gap: 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  flex-direction: column;
  margin-right: 6rem;
  overflow: auto;
  height: 80%;
  padding-bottom: 5rem;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.event-wrapper::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.event-wrapper {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.event-item {
  background-color: var(--color-dark);
  border-radius: 5px;
  padding: 2rem 2rem 1rem;
}

.event-title {
  color: var(--color-main);
  text-decoration: none;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  position: relative;
}

.event-description {
  font-weight: 500;
  margin-bottom: 1.5rem;
  color: var(--color-light);
}

hr {
  height: 1px;
  border: none;
  background-color: #545454;
}
.profile {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 3px;
  height: 2rem;
  width: 2rem;
  border-radius: 1rem;
  text-align: center;
  border: 1px solid var(--color-main);
  margin-right: 0.7rem;
}

.addition-description {
  display: flex;
  position: relative;
}

.event-attendees {
  display: flex;
  align-items: center;
  color: var(--color-light-gray);
  font-size: 14px;
}

.rightFloat {
  position: absolute;
  right: 0;
  width: 9rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.rightFloat span {
  padding: 5px 1.5rem;
  background-color: var(--color-dark-medium);
  color: var(--color-light-gray);
  border-radius: 5rem;
  font-weight: 100;
  font-size: 12px;
  margin-right: 1rem;
}

.date-time-container {
  position: absolute;
  right: 0;
  width: 15rem;
}

.date-time-container span {
  float: right;
  background-color: var(--color-light-gray);
  color: var(--color-dark);
  padding: 2px 1rem;
  border-radius: 1rem;
}

